import React from 'react';
import './Header.css';

/*======================================================================
// This stateless functional component will display the header.
======================================================================*/
const Header = () => {
    return (
        <header className="header">
            <h1>Welcome to Versium People Search</h1>
        </header>
    )
}

export default Header;


